.inStock {
	background: #fff;
	height: calc(100vh - 160px);
	overflow: auto;
	.header {
		width: 100%;
		height: 60px;
		padding: 0 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 12px;
		color: #666;
		border-bottom: 1px solid #E9E9E9;
		margin-bottom: 0; }
	.body {
		width: 100%;
		padding: 20px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		.filter {
			width: 100%;
			height: 120px;
			background: #F6F7FA;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			padding: 20px;
			margin-top: 16px;
			.left {
				width: 760px;
				display: flex;
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				li {
					width: 360px;
					font-size: 12px;
					color: #666;
					margin-right: 20px;
					display: flex;
					align-items: center;
					.ant-input {
						width: 300px;
						height: 30px; } }
				.needMargin {
					height: 30px;
					margin-bottom: 20px; }
				.ant-calendar-picker-input {
					height: 30px !important; } }
			.right {
				flex: 1;
				height: 100%;
				display: flex;
				justify-content: flex-start;
				align-items: flex-end;
				font-size: 12px;
				color: #4f8963;
				.button {
					margin-right: 10px; }
				.clear {
					margin-left: 20px; } } }
		.chart {
			width: 100%; } } }
