.tag_manage {
	background: #fff;
	height: calc(100vh - 144px); }
.ant-popover-inner-content {
	box-shadow: 0 1px 6px rgba(0,0,0,0.2); }
.header_tag {
	padding: 0 20px;
	height: 60px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	border-bottom: 1px solid #E9E9E9;
	margin-bottom: 0;
	.createNew {
		.fBtn {
			margin-left: 30px; }
		.btnAdd {
			margin: 0;
			margin-right: 10px; } } }
.tagGroups {
	padding: 0 20px;
	margin-top: 16px;
	position: relative;
	.popopver {
		z-index: 10000;
		position: absolute;
		right: 20px;
		top: 4px;
		font-size: 12px;
		color: #666;
		i {
			font-size: 14px;
			color: #666;
			margin-left: 5px; } }
	.ant-tabs-bar {
		border: 0;
		margin-bottom: 4px; }
	.ant-tabs-nav-wrap {
		height: 28px;
		.ant-tabs-tab {
			padding: 0!important;
			background: transparent !important;
			div {
				padding: 0 16px; }
			color: #666;
			font-size: 12px;
			border: 1px solid #d9d9d9!important;
			.ant-tabs-close-x {
				color: #fff!important;
				display: none; } } }
	.ant-tabs-tab-active {
		border: 2px solid #4f9863;
		.ant-tabs-close-x {
			display: inline-block !important; } }
	.chart_tag {
		.column {
			width: 33.3% !important;
			border-bottom: 0!important; } } }
.singleTab {
	background: #FF9C4B!important; }

.u_confirm_header {
	font-size: 14px;
	color: #666;
	display: flex;
	justify-content: space-between;
	align-items: center;
	i {
		font-size: 14px;
		color: #666; } }
.U_confirm {
	width: 100%;
	height: 30px;
	display: flex;
	font-size: 12px;
	color: #666;
	justify-content: center;
	align-items: center;
	i {
		font-size: 14px;
		color: #FF9C4B;
		margin-right: 10px; } }
.ant-modal-confirm-btns {
	margin: 0;
	width: 100%;
	padding: 0 30px;
	display: flex;
	justify-content: center;
	align-items: center; }
.ant-modal-confirm-content {
	margin-top: 20px !important; }
.ant-modal-confirm .ant-modal-body {
	padding: 20px 30px 30px 30px !important; }

