.userHeader {
	width: 100%;
	height: 32px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	.headerLeft {
		display: flex;
		justify-content: center;
		align-items: center;
		h4 {
			font-size: 12px;
			font-weight: normal;
			color: #4F9863;
			margin: 0 25px;
			cursor: pointer; }
		button {
			margin-right: 10px; } } }
.custom {
	z-index: 1111;
	background: #fff;
	position: absolute;
	right: 36px;
	width: 434px;
	background: rgba(255,255,255,1);
	box-shadow: 0 0 15px rgba(0,0,0,0.2);
	padding: 10px;
	opacity: 1;
	border-radius: 2px; }
.u_chart {
	margin-top: 15px;
	.ant-checkbox-indeterminate .ant-checkbox-inner {
		border: 0;
		background: #4f9863;
		&:after {
			background: #fff;
			height: 2px !important; } }
	.ant-table-row {
		td {
			border-bottom: 0 !important; } } }
.pagination {
	height: 60px;
	display: flex;
	justify-content: flex-end;
	align-items: center; }


.u_confirm_header {
	font-size: 14px;
	color: #666;
	display: flex;
	justify-content: space-between;
	align-items: center;
	i {
		font-size: 14px;
		color: #666; } }
.U_confirm {
	width: 100%;
	height: 50px;
	display: flex;
	font-size: 12px;
	color: #666;
	justify-content: center;
	align-items: center;
	i {
		font-size: 14px;
		color: #FF9C4B;
		margin-right: 10px; } }
.ant-modal-confirm-btns {
	margin: 0;
	width: 100%;
	padding: 0 30px;
	display: flex;
	justify-content: space-between;
	align-items: center; }

.f_com {
	.e_btn {
		border: 1px solid #d9d9d9!important;
		color: #666!important;
		&:hover {
			border: 1px solid #4f8963!important;
			color: #4f8963 !important; } } }
