.shopDetail {
	ul {
		list-style: none;
		padding: 0;
		margin: 0; }
	.u_top {
		width: 100%;
		font-size: 12px;
		color: #666;
		background: #fff;
		border-bottom: 10px solid #F0F0F0;
		.u_header {
			width: 100%;
			height: 60px;
			padding: 0 20px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid #E9E9E9; }
		.u_body_one {
			.u_body_top {
				width: 100%;
				display: flex;
				justify-content: flex-start;
				align-items: flex-start;
				padding: 20px 20px;
				position: relative;
				li {
					width: 25%;
					margin-left: 30px;
					img {
						width: 100px;
						height: 100px;
						display: block;
						border-radius: 50%;
						img {
							width: 100%;
							height: 100%;
							display: block;
							border-radius: 50%; } } }
				.firstChild {
					width: 15%; }
				.btns {
					display: flex;
					justify-content: flex-end;
					align-items: center;
					position: absolute;
					right: 20px;
					bottom: 10px;
					button {
						margin-left: 10px; } } } } }


	.u_body_two {
		width: 100%;
		border: 0!important;
		background: #fff;
		.t_header {
			width: 100%;
			height: 50px;
			padding: 0 20px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid #E9E9E9;
			h5 {
				font-weight: normal;
				font-size: 12px;
				color: #666;
				margin: 0; }
			.u_detail {
				color: #4f9863;
				cursor: pointer; } }
		ul {
			display: flex;
			justify-content: center;
			align-items: center;
			border-bottom: 10px solid #F0F0F0;
			padding: 30px 0;
			li {
				width: 25%;
				background: #fff;
				height: 80px;
				padding: 0 20px;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				border-right: 1px solid #F0F0F0;
				.adjust {
					position: relative;
					.overdraft {
						position: absolute;
						bottom: -30px;
						color: #999;
						font-size: 12px;
						.adjustOverdraft {
							position: absolute;
							right: 40px;
							font-size: 12px;
							display: inline-block;
							cursor: pointer;
							&:hover {
								color: #4F9863; } } } }
				&:last-child {
					border-right: 0; }
				.adjustBalance {
					font-size: 12px;
					margin-left: 10px;
					position: absolute;
					bottom: 25px;
					right: 40px;
					cursor: pointer; }
				h3 {
					width: 100%;
					height: 40px;
					display: flex;
					justify-content: center;
					align-items: center;
					font-weight: normal;
					font-size: 12px;
					color: #666;
					margin: 0; }
				div {
					width: 100%;
					padding: 20px 0;
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: 24px;
					color: #4F9863;
					position: relative; } } } }

	.u_body_three {
		font-size: 12px;
		color: #666;
		.u_body_three_list {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			&>li {
				width: 50%;
				height: 182px;
				background: #fff;
				flex-direction: column;
				padding: 0 20px;
				border-right: 10px solid #F0F0F0;
				border-bottom: 10px solid #F0F0F0;
				&:nth-child(2n) {
					border-right: 0; }
				.li_header {
					width: 100%;
					height: 50px;
					display: flex;
					font-size: 12px;
					color: #666;
					justify-content: center;
					align-items: center;
					border-bottom: 1px solid #E9E9E9;
					position: relative;
					span {
						font-size: 12px;
						color: #4f9863;
						position: absolute;
						right: 0;
						cursor: pointer; } }
				.li_body {
					ul {
						display: flex;
						justify-content: center;
						align-items: center;
						padding: 20px 0;
						li {
							flex: 1;
							height: 90px;
							padding: 0 20px;
							border-right: 1px solid #E9E9E9;
							display: flex;
							justify-content: flex-start;

							flex-direction: column;
							&:last-child {
								border-right : 0; }
							h4 {
								font-weight: normal;
								margin-bottom: 15px;
								color: #666;
								span {
									font-size: 24px;
									color: #4F9863;
									margin-left: 20px; } }
							.d_detail {
								display: flex;
								justify-content: center;
								align-items: center;
								flex-wrap: wrap;
								span {
									width: 50%; } } }
						.centerLi {
							display: flex;
							justify-content: center;
							align-items: center; } } } } } }


	.u_body_four {
		display: flex;
		justify-content: center;
		align-items: flex-start;
		background: #fff;
		border-bottom: 10px solid #F0F0F0;
		&>li {
			width: 33.3%;
			height: 225px;
			border-right: 10px solid #F0F0F0;
			padding: 0 20px;
			&:last-child {
				border-right: 0; }
			.u_body_four_header {
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 12px;
				color: #666;
				height: 50px;
				border-bottom: 1px solid #E9E9E9; }
			.u_body_four_body {
				display: flex;
				justify-content: center;
				align-items: center;
				li {
					width: 33.3%;
					padding: 20px 12px;
					span {
						display: inline-block;
						margin-bottom: 20px;
						font-size: 12px;
						color: #666; }
					h4 {
						font-weight: normal;
						font-size: 24px;
						color: #4F9863; } }

				.u_body_four_body_s {
					width: 50%;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column; } } } }

	.u_body_groups {
		background: #fff;
		.group_header {
			width: 100%;
			height: 50px;
			padding: 0 20px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			font-size: 12px;
			color: #666;
			border-bottom: 1px solid #E9E9E9; }
		.group_tags {
			width: 100%;
			padding: 20px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			.ant-tag {
				display: inline-block;
				height: 28px;
				line-height: 16px;
				margin-top: 1px;
				padding: 6px 8px;
				border: 1px solid #4F9863;
				background: #F1FCF4;
				color: #4F9863;
				font-size: 12px;
				border-radius: 2px;
				.anticon-close {
					font-size: 8px;
					color: #4F9863; } } } } }


