
.classification {
	.ant-table-thead {
		tr {
			th {
				&:first-child {
					padding-left: 40px!important; } } } }
	.ant-table-expanded-row {
		transition: all 0.5s !important;
		background: #fff; }
	.ant-table-row-expand-icon {
		border: none !important;
		background: transparent !important;
		position: relative;
		&:after {
			position: absolute;
			content: "";
			display: block;
			width: 8px;
			height: 8px;
			border: #666 solid;
			border-width: 1px 1px 0 0;
			transform: rotate(45deg);
			left: 7px;
			top: 7px; } }
	.ant-table-row-expanded {
		position: relative;
		&:after {
			position: absolute;
			content: "";
			display: block;
			width: 8px;
			height: 8px;
			border: #666 solid;
			border-width: 1px 1px 0 0;
			transform: rotate(135deg);
			left: 10px;
			top: 3px; } }
	.ant-table-tbody {
		span {
			color: #4f8963; } }
	.operation {
		color: #4f8963;
		span {
			&:first-child {
				margin-right: 15px; } } } }

.innerTable {
	margin-left: -72px !important;
	.ant-table-row {
		td {
			&:first-child {
				width: 41.5%;
				padding-left: 72px; }
			&:last-child {
				margin-left: 30px !important;
				padding-left: -50px !important;
				.operation {
					span {
						color: #666!important; } } } } } }

.goodsNames {
	display: flex;
	justify-content: center;
	align-items: center;
	span {
		flex: 2!important;
		max-width: 119px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap; }
	i {
		flex: 1 !important; } }
