
ul {
	list-style: none;
	padding: 0;
	margin: 0; }
.u_top {
	width: 100%;
	font-size: 12px;
	color: #666;
	background: #fff;
	border-bottom: 10px solid #F0F0F0;
	.u_header {
		width: 100%;
		height: 60px;
		padding: 0 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #E9E9E9; }
	.u_body {
		.u_body_top {
			width: 100%;
			height: 150px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			padding: 0 20px;
			border-bottom: 10px solid #F0F0F0;
			li {
				width: 25%;
				h3 {
					width: 100px;
					height: 100px;
					display: block;
					border-radius: 50%;
					img {
						width: 100%;
						height: 100%;
						display: block;
						border-radius: 50%; } } }
			.firstChild {
				width: 15%; } }
		.u_body_bottom {
			width: 100%;
			padding: 0 20px;
			height: 140px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			li {
				height: 80px;
				padding: 0 30px;
				border-right: 1px solid #E9E9E9;
				display: flex;
				justify-content: center;

				flex-direction: column;
				h4 {
					font-size: 24px;
					color: #666;
					font-weight: 400;
					margin: 0;
					span {
						color: #4F9863;
						font-size: 12px;
						margin-left: 10px;
						&:first-child {
							margin-left: 30px; } } }
				p {
					font-size: 12px;
					color: #666;
					font-weight: normal; }
				&:last-child {
					border-right: 0; } } } } }


.u_middle {
	width: 100%;
	border: 0!important;
	background: #fff;
	font-size: 12px;
	ul {
		background: #F0F0F0;
		display: flex;
		justify-content: center;
		align-items: center;
		border-bottom: 10px solid #F0F0F0;
		li {
			width: 33.3%;
			background: #fff;
			height: 161px;
			padding: 0 20px;
			border-right: 10px solid #F0F0F0;
			&:last-child {
				border-right: 0; }
			h3 {
				width: 100%;
				height: 40px;
				display: flex;
				justify-content: center;
				align-items: center;
				font-weight: normal;
				font-size: 12px;
				color: #666;
				margin: 0;
				border-bottom: 1px solid #E9E9E9;
				position: relative;
				.jump {
					position: absolute;
					right: 0;
					color: #4f9863;
					font-size: 12px;
					cursor: pointer; } }
			div {
				width: 100%;
				padding: 20px 0;
				display: flex;
				justify-content: center;
				align-items: center;
				span {
					&:first-child {
						border-right: 1px solid #E9E9E9; }
					width: 50%;
					padding: 7px 0;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column;
					p {
						color: #4F9863;
						font-size: 24px;
						margin-top: 20px;
						margin-bottom: 0; } } } } } }

.u_group {
	width: 100%;
	background: #fff;
	border-bottom: 10px solid #F0F0F0;
	.ant-tabs-nav-scroll {
		display: none; }
	.u_group_header {
		width: 100%;
		height: 60px;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 0 20px;
		border-bottom: 1px solid #E9E9E9;
		span {
			font-size: 12px;
			color: #666;
			padding: 6px 15px;
			border: 1px solid #D9D9D9; }
		.left {
			border-radius-topleft: 2px;
			border-radius-bottomleft: 2px; }
		.right {
			border-radius-topright: 2px;
			border-radius-bottomright: 2px;
			margin-left: -1px; }
		.activeG {
			background: #4F9863;
			color: #fff!important;
			border: 1px solid #4F9863; } }
	.u_group_body {
		width: 100%;
		padding: 20px;
		.ant-tabs-bar {
			margin: 0;
			border: 0; }
		.ant-tag {
			display: inline-block;
			height: 28px;
			line-height: 16px;
			margin-top: 1px;
			padding: 6px 8px;
			border: 1px solid #4F9863;
			background: #F1FCF4;
			color: #4F9863;
			font-size: 12px;
			border-radius: 2px;
			.anticon-close {
				font-size: 8px;
				color: #4F9863; } } } }
