.user_add {
	z-index: 999;
	.ant-modal-body {
		padding: 0; } }
.t_header {
	height: 50px;
	padding: 0 20px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 12px;
	color: #4F9863;
	cursor: pointer;
	i {
		font-size: 14px;
		color: #4F9863;
		margin-right: 5px; } }
.t_body {
	padding: 20px 30px !important;
	border-top: 1px solid #E9E9E9;
	li {
		margin: 0; } }

.addTag {
	.ant-select-selection-selected-value {
		line-height: 28px !important; }
	.ant-modal-footer {
		div {
			button {
				&:first-child {
					color: red; } } } } }
.tags {
	background: #F4F4F4;
	border-radius: 2px;
	margin-top: 10px;
	padding: 10px;
	.ant-tag {
		height: 28px !important;
		padding: 6px 8px;
		border:  1px solid #D9D9D9;
		background: #fff;
		border-radius: 2px;
		color: #666;
		font-size: 12px;
		display: flex;
		justify-content: center;
		align-items: center; }
	.ant-tag-checkable {
		display: inline-flex; }
	.ant-tag-checkable-checked {
		border: 1px solid #4F9863;
		color: #4F9863;
		background: #F1FCF4; } }
.t_group {
	background: #fff;
	padding: 0 10px 10px 10px;
	margin: 0; }
