.m_message {
	background: #fff;
	.ant-tabs-nav-scroll {
		width: 100%;
		height: 60px;
		padding: 0 20px;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		border-bottom: 1px solid #e9e9e9; }
	.ant-tabs-nav-wrap {
		border-bottom: 1px solid #e9e9e9; }
